*, *::after, *::before {
    box-sizing: border-box;
}
.ac-custom {
    margin: 0 auto;
    max-width: 900px;
    padding: 0 3em;
}
.ac-custom ul, .ac-custom ol {
    list-style: outside none none;
    margin: 0 auto;
    max-width: 800px;
    padding: 0;
}
.ac-custom li {
    margin: 0 auto;
    padding: 2em 0;
    position: relative;
}
.ac-custom input[type="checkbox"], .ac-custom input[type="radio"] {
    display: inline-block;
    opacity: 0;
    vertical-align: middle;
    z-index: 100;
}
.ac-custom input[type="checkbox"], .ac-custom input[type="radio"], .ac-custom label::before {
    cursor: pointer;
    height: 50px;
    left: 0;
    margin-top: -25px;
    position: absolute;
    top: 50%;
    width: 50px;
}
input[type="checkbox"], input[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}
button, input {
    line-height: normal;
}
button, input, select, textarea {
    font-family: inherit;
    font-size: 100%;
    margin: 0;
}
.ac-radio label::before {
    border-radius: 50%;
}
.ac-custom label::before {
    border: 4px solid #fff;
    content: "";
    transition: opacity 0.3s ease 0s;
}
.ac-custom label {
    color: rgba(0, 0, 0, 0.2);
    cursor: pointer;
    display: inline-block;
    font-size: 2em;
    padding: 0 0 0 80px;
    position: relative;
    transition: color 0.3s ease 0s;
    vertical-align: top;
}
.ac-custom svg {
    height: 40px;
    left: 5px;
    margin-top: -20px;
    pointer-events: none;
    position: absolute;
    top: 50%;
    width: 40px;
}
svg:not(:root) {
    overflow: hidden;
}
